import { onMounted, ref } from 'vue';

export default {
  //   components: {
  //     cScrollbar,
  //   },
  setup() {
    const scrollbarRef = ref();
    onMounted(() => {
      scrollbarRef.value.setScrollTop(100);
      scrollbarRef.value.setScrollLeft(10);
    });
    return {
      scrollbarRef,
    };
  },
};
